import React from 'react'
import { Container, Grid, Box, TextField, Typography, Button } from '@mui/material'
import { useHistory } from 'react-router-dom'
import axios from 'axios'


const LoginPage = () => {
    const [state, setState] = React.useState({})
    const history = useHistory()

    function loginTry() {
        axios.post(`${process.env.REACT_APP_BASE_URL}/user/login`, {
            email: state.email,
            password: state.password
        }).then((res) => {
            if (!res.data.success || !res.data.data.email) {
                alert(res.data.msg)
                return
            }
            localStorage.setItem('webtorrent_token', res.data.data.password)
            history.push('/fly')

        }).catch((err) => {
            console.log(err)
            alert("Internet error")
        })
    }

    return (
        <Grid sx={{ padding: 2 }} bgcolor={'black'} alignItems={'center'} justifyContent='center' container height={'100vh'}>
            <Grid item>
                <Container maxWidth='sm' sx={{ bgcolor: '#1A1A1A', padding: 5, borderRadius: 10 }} >
                    <Grid container direction={'column'} spacing={2}>
                        <Grid item>
                            <Typography color={'white'} variant='h6' fontWeight={'bold'} >Hi, Human</Typography>
                            <Typography color={'gray'} fontSize={15}  >I am WebTorrent allows you to download torrent in the future way.</Typography>
                        </Grid>
                        <Grid item>
                            <TextField onChange={(e) => setState({ ...state, email: e.target.value })} size='small' label="Email" fullWidth />
                        </Grid>
                        <Grid item>
                            <TextField type='password' onChange={(e) => setState({ ...state, password: e.target.value })} size='small' label="Password" fullWidth />
                        </Grid>
                        <Grid item>
                            <Button onClick={loginTry} disabled={state.email && state.password ? false : true} fullWidth color='inherit' variant='contained' sx={{ borderRadius: 10 }} >
                                Try
                            </Button>
                        </Grid>
                    </Grid>
                </Container>
            </Grid>
        </Grid>
    )
}

export default LoginPage