import { Delete, Download, ExpandMore, LocalHospital, Visibility } from '@mui/icons-material'
import { Accordion, AccordionDetails, AccordionSummary, Grid, IconButton, LinearProgress, Tab, Tabs, Typography } from '@mui/material'
import { Box, Container } from '@mui/system'
import axios from 'axios'
import React from 'react'
import { useHistory } from 'react-router-dom'
import Header from './Header'


function LinearProgressWithLabel(props) {
    return (
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Box sx={{ width: '100%', mr: 1 }}>
                <LinearProgress variant="determinate" {...props} />
            </Box>
            <Box sx={{ minWidth: 35 }}>
                <Typography variant="body2" color="text.secondary">{`${Math.round(
                    props.value,
                )}%`}</Typography>
            </Box>
        </Box>
    );
}

const DownloadsScreen = () => {
    const [state, setState] = React.useState({
        downloaded: []
    })

    const [value, setValue] = React.useState('one');
    const [pD, setPD] = React.useState([])
    const [load, setLoad] = React.useState(false)
    const token = localStorage.getItem('webtorrent_token')
    const history = useHistory()

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };


    React.useEffect(() => {
        // setLoad(true)
        setState({ ...state, loading: true })
        axios.get(`${process.env.REACT_APP_BASE_URL}/downloads/files`)
            .then((res) => {
                setState({ ...state, loading: false, downloaded: res.data.data })
            }).catch((err) => {
                alert("Server error")
                console.log(err)
            })

        axios.get(`${process.env.REACT_APP_BASE_URL}/downloads/get_pending_downloads`)
            .then((res) => {
                setLoad(false)
                setPD(res.data.data)
            }).catch((err) => {
                alert("Server error")
                console.log(err)
            })
    }, [])


    function deleteOne(e) {
        axios.post(`${process.env.REACT_APP_BASE_URL}/downloads/delete_one`, {
            file_name: e
        }, {
            headers: {
                Authorization: "Bearer " + token
            }
        }).then((res) => {
            if (res.data.login) {
                localStorage.removeItem('webtorrent_admin')
                history.push('/')
                return
            }
            if (res.data.success) {
                const index = state.downloaded.indexOf(e)
                if (index > -1) {
                    state.downloaded.splice(index, 1)
                }
                setState({ ...state, downloaded: [].concat(state.downloaded) })
            }
            alert(res.data.msg)
        }).catch((err) => console.log(err))
    }

    function deleteMongo(e) {
        axios.post(`${process.env.REACT_APP_BASE_URL}/downloads/delete_mongo`, {
            id: e._id
        }, {
            headers: {
                Authorization: "Bearer " + token
            }
        }).then((res) => {
            if (res.data.login) {
                localStorage.removeItem('webtorrent_admin')
                history.push('/')
                return
            }
            if (res.data.success) {
                const index = pD.indexOf(e)
                if (index > -1) {
                    pD.splice(index, 1)
                }
                setPD([].concat(pD))
            }
            alert(res.data.msg)
        }).catch((err) => console.log(err))
    }


    return (
        <Box minHeight={'100vh'} bgcolor='black' p={2}>
            <Container maxWidth='lg'>
                <Header />
                <Box mt={4} >
                    <Tabs
                        variant='scrollable'
                        value={value}
                        onChange={handleChange}
                        textColor="secondary"
                    >
                        <Tab value="one" label="Downloading" />
                        <Tab value="two" label="Downloaded" />
                    </Tabs>

                    {
                        load || state.loading ? (
                            <LinearProgress />
                        ) : (
                            <Box mt={2} >
                                {
                                    value === 'one' ? (
                                        <Box  >
                                            {
                                                pD.length > 0 ? (
                                                    pD.map((val, key) => {
                                                        return (
                                                            <Box key={key} mb={2} bgcolor={'#202020'} p={1} borderRadius={2} >
                                                                <Grid container alignItems={'center'} justifyContent={'space-between'}>
                                                                    <Grid item>
                                                                        <Typography fontSize={13} color='white' >{val.title}:- </Typography>
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <IconButton onClick={() => {
                                                                            deleteMongo(val)
                                                                        }} size='small' >
                                                                            <Delete />
                                                                        </IconButton>
                                                                    </Grid>
                                                                </Grid>
                                                                <Box mt={2} >
                                                                    {val.percentage === undefined ?
                                                                        <Typography fontSize={15} color='gray' >No response from Peers</Typography> :
                                                                        <LinearProgressWithLabel value={val.percentage} />}
                                                                </Box>
                                                            </Box>
                                                        )
                                                    })
                                                ) : (
                                                    <Typography fontSize={12} color='gray' >Its lonley here</Typography>
                                                )
                                            }
                                        </Box>
                                    ) :
                                        <Box mt={5} >
                                            <Grid container spacing={2} direction='column'>
                                                {
                                                    state.downloaded.length > 0 ? (
                                                        state.downloaded.map((val, key) => {
                                                            return (
                                                                <Box key={key} mb={2} bgcolor={'#202020'} p={1} borderRadius={2} >
                                                                    <Grid container alignItems={'center'} justifyContent={'space-between'}>
                                                                        <Grid item>
                                                                            <Typography fontSize={13} color='white' >{val}</Typography>
                                                                        </Grid>
                                                                        <Grid item>
                                                                            <IconButton onClick={() => {
                                                                                window.open(`/torrent/${val}`)
                                                                            }} >
                                                                                <Download />
                                                                            </IconButton>
                                                                            <IconButton onClick={() => {
                                                                                deleteOne(val)
                                                                            }} >
                                                                                <Delete />
                                                                            </IconButton>
                                                                        </Grid>
                                                                    </Grid>
                                                                </Box>
                                                            )
                                                        })
                                                    ) : (
                                                        <Box p={2}>

                                                            <Typography fontSize={12} color='gray' >Its lonley here.</Typography>
                                                        </Box>
                                                    )
                                                }
                                            </Grid>
                                        </Box>
                                }
                            </Box>
                        )
                    }
                </Box>

            </Container>
        </Box>
    )
}

export default DownloadsScreen