import React from 'react'
import { Box, Typography, Container, Button, Grid, TextField, InputAdornment, IconButton, CircularProgress, Dialog, Divider } from '@mui/material'
import { Link, useHistory } from 'react-router-dom'
import axios from 'axios'
import { Search, Download, Visibility, DriveFileMove, CloudDownload, Logout, Settings } from '@mui/icons-material'


const MainPage = () => {
    const token = localStorage.getItem('webtorrent_token')
    const history = useHistory()
    const [state, setState] = React.useState({
        data: [],
        data_detail: ""
    })
    const [load, setLoad] = React.useState(false)
    const [dialog, setDialog] = React.useState(false)

    const [popup, setPopup] = React.useState({})


    function searchBey(e) {
        setState({ ...state, loading: true })
        axios.post(`${process.env.REACT_APP_BASE_URL}/user/search`, {
            query: e
        }, {
            headers: {
                Authorization: "Bearer " + token
            }
        })
            .then((res) => {
                if (res.data.logout) {
                    localStorage.removeItem('webtorrent_token')
                    history.push('/')
                    return
                }

                setState({ ...state, loading: false, data: res.data.data || [] })
            }).catch((err) => console.log(err))
    }


    function searchBeyDetail(e) {
        setLoad(true)
        setDialog(true)
        axios.post(`${process.env.REACT_APP_BASE_URL}/user/search_detail`, {
            id: e,
            title: "Songs"
        }, {
            headers: {
                Authorization: "Bearer " + token
            }
        })
            .then((res) => {
                setState({ ...state, data_detail: res.data.data })
                setLoad(false)
                console.log(res.data)
            }).catch((err) => console.log(err))
    }

    function bytesToSize(bytes) {
        var sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
        if (bytes == 0) return '0 Byte';
        var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
        return Math.round(bytes / Math.pow(1024, i), 2) + ' ' + sizes[i];
    }

    function downloadStart(e, name) {
        alert("Download Started \nIf peers are active your downloads will be done very soon.")
        axios.post(`${process.env.REACT_APP_BASE_URL}/downloads/start_download`, {
            url: e,
            title: name
        }, {
            headers: {
                Authorization: "Bearer " + token
            }
        }).then((res) => {

            if (res.data.logout) {
                localStorage.removeItem('webtorrent_token')
                history.push('/')
            }

            alert("Download Done!")
        }).catch((err) => {
            console.log(err)
            alert("Server error")
        })
    }

    function getUser() {
        axios.get(`${process.env.REACT_APP_BASE_URL}/user/get_email`, {
            headers: {
                Authorization: "Bearer " + token
            }
        }).then((res) => {
            setPopup({ ...popup, email: res.data.data })
        }).catch((err) => console.log(err))
    }

    function changePw() {
        axios.post(`${process.env.REACT_APP_BASE_URL}/user/change_pw`, {
            email: popup.email,
            password: popup.password
        }, {
            headers: {
                Authorization: "Bearer " + popup.old_password
            }
        }).then((res) => {
            if (!res.data.success) {
                alert("Check your old password")
                return
            }
            alert(res.data.msg)
        }).catch((err) => console.log(err))
    }

    React.useEffect(() => {
        if (!token) {
            return
        }
        getUser()
    }, [token])

    return (
        <Grid container sx={{ minHeight: '100vh', bgcolor: 'black', padding: 2, alignItems: 'center', justifyContent: 'center' }}>

            <Dialog open={popup.dialog} onClose={() => setPopup({ ...popup, dialog: false })} >
                <Box p={4}>
                    <Grid container direction={'column'} spacing={2}>
                        <Grid item>
                            <Typography fontWeight={'bold'} >Change your email/password</Typography>
                        </Grid>
                        <Grid item>
                            <TextField onChange={(e) => setPopup({ ...popup, email: e.target.value })} value={popup.email} label="Email" helperText="do not touch this field if you dont want to change email" />
                        </Grid>
                        <Grid item>
                            <TextField onChange={(e) => setPopup({ ...popup, old_password: e.target.value })} fullWidth label="Old Password" />
                        </Grid>
                        <Grid item>
                            <TextField onChange={(e) => setPopup({ ...popup, password: e.target.value })} fullWidth label="New Password" />
                        </Grid>
                        <Grid item>
                            <Button onClick={changePw} fullWidth variant='contained' sx={{ borderRadius: 10 }} >Save</Button>
                        </Grid>

                    </Grid>
                </Box>
            </Dialog>



            <Grid xs={12} sm={12} lg={12} item>
                <Container maxWidth='lg'>

                    <Dialog open={dialog} onClose={() => setDialog(false)} >
                        <Box p={2} >
                            {
                                load ? (
                                    <CircularProgress />
                                ) : (
                                    <Box p={2} >
                                        <Typography sx={{ whiteSpace: 'pre-wrap' }} >{state.data_detail.descr}</Typography>
                                        <Box mt={2} mb={2}>
                                            <Divider />
                                        </Box>
                                        <Typography fontSize={12} >IMDB: {state.data_detail.imdb}</Typography>
                                        <Typography fontSize={12} >Language: {state.data_detail.language}</Typography>
                                        <Typography fontSize={12} >Text Language: {state.data_detail.textlanguage}</Typography>
                                    </Box>
                                )
                            }
                        </Box>
                    </Dialog>

                    <Grid container direction={'row'} justifyContent={'space-between'} spacing={2} >
                        <Grid item>
                            <Box mb={3} >
                                <Typography color={'white'} variant='h5' >Whats on your head ?</Typography>
                                <Typography fontSize={15} color='gray' >A Torrent search engine along with server side download.</Typography>
                            </Box>
                        </Grid>
                        <Grid item>
                            <Grid container direction={'row'} spacing={1}>
                                <Grid item>
                                    <IconButton component={Link} to="downloaded" >
                                        <CloudDownload sx={{ height: 40, width: 40, color: 'lightgray' }} />
                                    </IconButton>
                                </Grid>
                                <Grid item>
                                    <IconButton onClick={() => {
                                        localStorage.removeItem('webtorrent_token')
                                        history.push('/')
                                    }} >
                                        <Logout sx={{ height: 40, width: 40, color: 'red' }} />
                                    </IconButton>
                                </Grid>
                                <Grid item>
                                    <IconButton onClick={() => {
                                        setPopup({ ...popup, dialog: true })
                                    }} >
                                        <Settings sx={{ height: 40, width: 40, color: '#b8bdff' }} />
                                    </IconButton>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>



                    <TextField onKeyPress={(e) => {
                        if (e.key === "Enter") {
                            console.log(e.target.value);
                            return
                        }
                        searchBey(e.target.value)
                    }} InputProps={{
                        endAdornment: <InputAdornment position="start">
                            {
                                state.loading ? (
                                    <CircularProgress size={20} />
                                ) : (
                                    null
                                )
                            }
                        </InputAdornment>,
                    }} label="Search Moives/Shows/Beep/Etc." fullWidth />


                    <Box mt={2} >
                        {
                            state.data.map((data, key) => {
                                return (
                                    <Box mb={2} key={key} borderRadius={2} bgcolor={'#1A1A1A'} p={2}>
                                        <Grid container justifyContent={'space-between'} direction='column' >
                                            <Grid item>
                                                <Box flexWrap={'wrap'} >
                                                    <Typography fontSize={15} color={'white'} >{data.name.length > 40 ? data.name.slice(0, 40) + "..." : data.name}</Typography>
                                                    <Typography color={'gray'} fontSize={12} >Size: {bytesToSize(data.size)}</Typography>
                                                    <Typography color={'gray'} fontSize={12} >File(s): {data.num_files}</Typography>
                                                    <Typography color={'gray'} fontSize={12} >Seeders: {data.seeders}</Typography>
                                                </Box>
                                            </Grid>
                                            <Grid item>
                                                <Grid container direction={'row'} spacing={2}>
                                                    <Grid item>
                                                        <IconButton onClick={() => {
                                                            downloadStart(data.info_hash, data.name)
                                                        }} >
                                                            <Download />
                                                        </IconButton>
                                                    </Grid>
                                                    <Grid item>
                                                        <IconButton onClick={() => searchBeyDetail(data.id)} >
                                                            <Visibility sx={{ color: '#FFBF00' }} />
                                                        </IconButton>
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Box>
                                )
                            })
                        }
                    </Box>


                    {/* <Button component={Link} to="/search" sx={{ borderRadius: 5, padding: 2, textTransform: 'none', minWidth: '50%' }} fullWidth variant='outlined'>
                        Search Moives/Shows/Etc
                    </Button> */}

                    {/* <Tasks />
                <HistoryComp /> */}


                </Container>
            </Grid>
        </Grid>
    )
}

export default MainPage