import React from 'react'
import { Switch } from 'react-router-dom'
import PrivateRoute from './routing/PrivateRoute'
import { useTheme, ThemeProvider, createTheme } from '@mui/material/styles';
import PublicRoute from './routing/PublicRoute'
import { CircularProgress, Box, IconButton } from '@mui/material'
import { Brightness4, Brightness7 } from '@mui/icons-material';
import LoginPage from './pages/LoginPage';
import MainPage from './pages/main/MainPage';
import DownloadsScreen from './pages/main/components/DownloadsScreen';
import FileManagerScreen from './pages/main/components/FileManagerScreen';
import HomePage from './pages/HomePage';
import InstallApp from './pages/InstallApp';
import DocScreen from './pages/DocScreen';

const ColorModeContext = React.createContext({ toggleColorMode: () => { } });

function MyApp() {
  const theme = useTheme();
  const colorMode = React.useContext(ColorModeContext);
  return (
    <Box
      sx={{
        display: 'flex',
        width: '100%',
        alignItems: 'center',
        justifyContent: 'center',
        bgcolor: 'background.default',
        color: 'text.primary',
        borderRadius: 1,
        p: 3,
      }}
    >
      {theme.palette.mode} mode
      <IconButton sx={{ ml: 1 }} onClick={colorMode.toggleColorMode} color="inherit">
        {theme.palette.mode === 'dark' ? <Brightness7 /> : <Brightness4 />}
      </IconButton>
    </Box>
  );
}

export default function ToggleColorMode() {

  React.useEffect(() => {
    setMode(localStorage.getItem('theme') ? (localStorage.getItem('theme')) : (localStorage.setItem('theme', 'dark'), "dark"))
  }, [])

  const [mode, setMode] = React.useState('dark');
  const colorMode = React.useMemo(
    () => ({
      toggleColorMode: () => {
        setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
      },
    }),
    [],
  );

  const theme = React.useMemo(
    () =>
      createTheme({
        palette: {
          mode,
        },
      }),
    [mode],
  );

  return (
    <ColorModeContext.Provider value={colorMode}>

      <ThemeProvider theme={theme}>
        <Switch>
          <PublicRoute exact path="/" component={HomePage} />
          <PublicRoute exact path="/install" component={InstallApp} />
          <PublicRoute exact path="/v1/doc" component={DocScreen} />
          <PublicRoute exact path="/login" component={LoginPage} />
          <PrivateRoute exact path="/fly" component={MainPage} />
          <PrivateRoute exact path="/downloaded" component={DownloadsScreen} />
          <PrivateRoute exact path="/files" component={FileManagerScreen} />
        </Switch>
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
}